import React from 'react'
import styled from 'styled-components'
import { Heading } from '../heading'
import { Paragraph } from '../paragraph'
import { graphql, StaticQuery } from "gatsby"
import Img from "gatsby-image"
import { Controller, Scene } from 'react-scrollmagic'
import { TimelineMax, TweenMax } from 'gsap'
import { useOnEnterLeaveTransition } from '../../../hooks/useOnEnterLeaveTransition'

export const ImageSlider = () => {
    const handleAnimation = useOnEnterLeaveTransition(() => {
        var tl = new TimelineMax({ delay: 0 });
        tl.add(TweenMax.to(".FadeInFullImg", 0.4, { webkitClipPath: 'inset(0% 0% 0% 0% )' }));
        tl.play()
    }, () => {
        var tl = new TimelineMax({ delay: 0 });
        tl.add(TweenMax.to(".FadeInFullImg", 0.4, { webkitClipPath: 'inset(0% 100% 0% 0% )' }));
        tl.play()
    })

    return <StaticQuery
        query={graphql`
                    query {
                        first: file(relativePath: { eq: "fullimg1.jpg" }) {
                            childImageSharp {
                                fluid {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                `}
        render={data => <Controller vertical key="spa-controller">
            <Scene duration={500} triggerHook="onEnter" offset={500}>
                {(progress, event) => {
                    handleAnimation(event)
                    return <ImagesSliderWrap>
                        <FullImg1 className="FadeInFullImg"
                            fluid={data.first.childImageSharp.fluid}
                            alt="Gatsby Docs are awesome"
                        />
                    </ImagesSliderWrap>

                }}</Scene>
        </Controller>
        } />
}

const ImagesSliderWrap = styled.div`
    height: 700px;
    width: 100vw;
    .FadeInFullImg{
        clip-path: inset(0% 100% 0% 0% )
    }
`

const FullImg1 = styled(Img)`
    z-index: 1;
    height:700px;
    width: 100%;
    transition: all 0.4s ease-in;
    
`
