import React from 'react'
import styled from 'styled-components'
import { Heading } from '../heading'
import { Paragraph } from '../paragraph'
import { graphql, StaticQuery } from "gatsby"
import Img from "gatsby-image"
import { Sizes, useWindowSize, Device } from "../../global/sizes"
import { Controller, Scene } from 'react-scrollmagic'
import { TimelineMax, TweenMax } from 'gsap'
import { useOnEnterLeaveTransition } from '../../../hooks/useOnEnterLeaveTransition'

export const Nature = () => {
    const handleAnimation = useOnEnterLeaveTransition(() => {
        var tl = new TimelineMax({ delay: 0 });
        tl.add(TweenMax.to(".NatFadeIn", 0.3, { opacity: 1, y: 0 }));
        tl.add(TweenMax.to(".NatFadeInNext", 0.3, { opacity: 1, y: 0, delay: 0.1 }));
        tl.add(TweenMax.to(".NatFadeInNext2", 0.3, { opacity: 1, y: 0, delay: 0.1 }));
        tl.add(TweenMax.to(".NatFadeInImg", 0.4, { webkitClipPath: 'inset(0% 0% 0% 0% )' }));
        tl.add(TweenMax.to(".NatFadeInImg2", 0.4, { webkitClipPath: 'inset(0% 0% 0% 0% )' }));
        tl.play()
    }, () => {
        var tl = new TimelineMax({ delay: 0 });
        tl.add(TweenMax.to(".NatFadeIn", 0.3, { opacity: 0, y: 20 }));
        tl.add(TweenMax.to(".NatFadeInNext", 0.3, { opacity: 0, y: 20 }));
        tl.add(TweenMax.to(".NatFadeInNext2", 0.3, { opacity: 0, y: 20 }));
        tl.add(TweenMax.to(".NatFadeInImg", 0.4, { webkitClipPath: 'inset(0% 100% 0% 0% )' }));
        tl.add(TweenMax.to(".NatFadeInImg2", 0.4, { webkitClipPath: 'inset(0% 0% 0% 100% )' }));
        tl.play()
    })

    return <StaticQuery
        query={graphql`
            query {
                first: file(relativePath: { eq: "pin.jpg" }) {
                    childImageSharp {
                        fluid {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
                second: file(relativePath: { eq: "hercule.jpg" }) {
                    childImageSharp {
                        fluid {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
                third: file(relativePath: { eq: "mountains-bg.jpg" }) {
                    childImageSharp {
                        fluid {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
        `}
        render={data => <Controller vertical key="short-history-controller">
            <Scene duration={500} triggerHook="onEnter" offset={500}>
                {(progress, event) => {
                    handleAnimation(event)
                    return <NatureHome>
                        <ContentHolder>
                            <ContentLeft>
                                <Heading level="2" className="NatFadeIn">Păsește pe urmele Impărătesei Sissi și al Impăratului Franz Joseph</Heading>
                                <NaturePhoto1 className="NatFadeInImg"
                                    fluid={data.first.childImageSharp.fluid}
                                    alt="Gatsby Docs are awesome"
                                />
                            </ContentLeft>
                            <ContentRight>
                                <Paragraph style={{}} className="NatFadeInNext">
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Interdum est viverra urna nunc. Etiam eu, tristique enim quis egestas.
                        <br /><br /><br />
                                    <b>Aer ionizat</b>
                                    <br /><br />
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Interdum est viverra urna nunc. Etiam eu, tristique enim quis egestas.
                        <br /><br /><br />
                                    <b>Pinul negru de banat</b>
                                    <br /><br />
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Interdum est viverra urna nunc. Etiam eu, tristique enim quis egestas.
                    </Paragraph>
                                <NaturePhoto2 className="NatFadeInImg2"
                                    fluid={data.second.childImageSharp.fluid}
                                    alt="Gatsby Docs are awesome"
                                />
                            </ContentRight>
                            <BackPhoto
                                fluid={data.third.childImageSharp.fluid}
                                alt="Gatsby Docs are awesome"
                            />
                        </ContentHolder>
                    </NatureHome>
                }}</Scene>
        </Controller>
        } />
}


const NatureHome = styled.div`
    min-height: 100vh;
    width: 100vw;
    background-color: white;
    .NatFadeIn, .NatFadeInNext, .NatFadeInNext2{
        opacity:0;
        transform: translateY(20px);
    }
    .NatFadeInImg{
        clip-path: inset(0% 100% 0% 0% )
    }
    .NatFadeInImg2{
        clip-path: inset(0% 0% 0% 100% )
    }
`

const ContentHolder = styled.div`
    padding-bottom: 60px;
    position: relative;
    display:flex;
    justify-content:space-between;
    flex-direction:row;
    max-width:1114px;
    margin-left:auto;
    margin-right:auto;
    margin-top:20px;
    width:80%;
    padding:200px 10%;
    z-index: 1;

    @media ${Device.tablet} {
        flex-direction: column;
        height:auto;
    }
`
const NaturePhoto1 = styled(Img)`
    z-index: 1;
    width: 100%;
    margin-top:60px;
    transition: all 0.4s ease-in;
`
const NaturePhoto2 = styled(Img)`
    z-index: 1;
    width: 100%;
    align-self:flex-end;
    transition: all 0.4s ease-in;
`
const ContentLeft = styled.div`
    z-index: 1;
    width: 47%;
    @media ${Device.tablet} {
        width:100%;
        margin-bottom:60px;
    }
`
const ContentRight = styled.div`
    z-index: 1;
    width: 47%;
    display:flex;
    justify-content:space-between;
    flex-direction:column;

    @media ${Device.tablet} {
        width:100%;
    }
`
const BackPhoto = styled(Img)`
    z-index: -1;
    position:absolute !important;
    max-width:900px;
    width:70%;
    left:30px;
    bottom:30%;
    height:auto;

    @media ${Device.tablet} {
        bottom:0;
    }
`